import Attack from "../js/attack";
import Count from "../js/count";
import StatusBlock from "../block/status";
import Query from "../js/query";
import DamageBlock from "../block/damage";
import Report from "../js/report";
import CureStatusBlock from "../block/cureStatus";
import OddBlock from "../block/odd";
import ExecuteBlock from "../block/execute";
import ActiveTacticsBlock from "../block/execute3";
import AfterDamageBlock from "../block/execute1";
import AttrBlock from "../block/attr";
import AttackExecuteBlock from "../block/execute4";
let qunSArr = [
    //
    {
        id : 5,
        name : "",
        type : "",
        odd : 100,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let index = 120;
            if(self.driver.version == ''){
                index = 100;
            }

            //
            let block = new DamageBlock(self,this,"",true,index,false,false,99);
            block.Init(self,null,false);
            let report = new Report();
            report.InitExecuteReport(2,self,`${index}%`);
            //
            let block1 = new StatusBlock(self,this,"",99);
            block1.Init(self,false);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 26,
        name : "",
        type : "",
        odd : 100,
        range : 3,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : true,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let a = new Attack();
            let cureValue = 85;
            let cureIndex = 0.75;
            let odd = 75;


            let res = a.SelectTarget(self,3,true,false,3,true,false,this);
            for(let i in res){
                let block = new CureStatusBlock(self,this,"",cureValue,cureIndex,99,0,odd);
                let work = block.Init(res[i],false);
                if(work){
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],`()`);
                }
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 45,
        name : "",
        type : "",
        odd : 35,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let atk = new Attack();
            let c = new Count();
            let q = new Query();
            let damageIndex = 120;//
            if(self.driver.version == ''){
                damageIndex = 100;
            }
            
            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            //
            let block = new StatusBlock(self,this,"",0);
            block.Init(self);
            for(let j = 0 ;j < 2;j++){
                let res = atk.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this);
                for(let i in res){
                    let damage = c.CountDamage(self,res[i],damageIndex,"",false,this);
                    atk.GetHurt(self,res[i],damage,false,"",this);
                }
                //
                if(j == 0){
                    for(let i in res){
                        let block = new DamageBlock(self,this,"",true,-999,false,false,1);
                        block.Init(res[i])
                    }
                }
            }
            let arr = [];
            for(let i in self.STATUS){
                if(self.STATUS[i].tactics != this) arr.push(self.STATUS[i]);
                else{
                    let report = new Report();
                    report.InitExecuteReport(3,self,`${self.STATUS[i].tactics.name}${self.STATUS[i].type}`)
                }
            }
            self.STATUS = arr;
            
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 31,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let a = new Attack();
            let q = new Query();
            let value = q.GetAdditionValue(self,26,'',0.2);//

            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,false,true,2,false,true,this);
            for(let i in res){
                let block1 = new DamageBlock(self,this,"",true,-value,false,false,3);
                let block2 = new DamageBlock(self,this,"",true,-value,false,false,3);
                let work1 =  block1.Init(res[i],null,false);
                if(work1){
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],`${value}%`);
                }
                let work2 = block2.Init(res[i],null,false);
                if(work2){
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],`${value}%`);
                }
            }
            //
            for(let i in res){
                let block = new StatusBlock(self,this,"",5,3);
                let work = block.Init(res[i],false);
                if(work){
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],`()`)
                }
            }
            
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 76,
        name : "",
        type : "",
        odd : 35,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let damageValue = q.GetAdditionValue(self,240,'',2.75);

            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this);
            let fri = a.SelectTarget(self,realRange,true,false,realTargetNumber,false,true,this);
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageValue,'',false,this,null);
                a.GetHurt(self,res[i],damage,false,'',this,true,null);
            }
            for(let i in fri){
                let block = new DamageBlock(self,this,'',false,-999,true,false,99,0);
                block.Init(fri[i],null,true);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 115,
        name : "",
        type : "",
        odd : 35,
        range : 3,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : [2,3],
        arms : [''],
        wait : 0,
        execute(self){
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let value = q.GetAdditionValue(self,40,'',0.25);
            let targetNumber = 2 + Math.floor(Math.random()*2);
            
            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,true,false,targetNumber,true,true,this);
            for(let i in res){
                for(let j in res[i].TacticsArr){
                    if(res[i].TacticsArr[j].type == ''){
                        let odd = new OddBlock(self,this,res[i].TacticsArr[j],100,1,0,0);
                        odd.Init(res[i],true,3);
                    }
                }
            }
            for(let i in res){
                let block = new DamageBlock(self,this,'',true,value,false,false,1,0);
                block.Init(res[i],null,true,3);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 117,
        name : "",
        type : "",
        odd : 100,
        range : 3,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : false,
        selectEnemy : false,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let value = 30;//
            let odd = 10;//

            let tag = '' + self.belong + self.position;
            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,true,false,2,false,false,this);
            let action = function(){
                let r = new Report();
                r.InitExecuteReport(2,self,``);
                let targetArr = [];
                //
                for(let i in res){
                    for(let j in res[i].TacticsArr){
                        if(res[i].TacticsArr[j].type == ''){
                            targetArr.push({
                                warrior : res[i],
                                tactics : res[i].TacticsArr[j]
                            });
                        }
                    }
                }
                let final = [];
                for(let i=0;i<self[tag] && targetArr.length > 0;i++){
                    let rand = Math.floor(Math.random()*targetArr.length);
                    final.push(targetArr[rand]);
                    targetArr.splice(rand,1);
                }
                for(let i in final){
                    //
                    let work1 = true;
                    let work2 = true;
                    for(let k in final[i].warrior.ODD_VAR){
                        let tgt = final[i].warrior.ODD_VAR[k]
                        if(tgt.tactics == that && tgt.duration == 0 && tgt.workTactics == final[i].tactics){
                            tgt.duration = 1;
                            let r = new Report();
                            r.InitExecuteReport(3,final[i].warrior,`${final[i].tactics.name}${tgt.value}%`);
                            work1 = false;
                        }
                    }
                    for(let k in final[i].warrior.DAMAGE_VAR){
                        let tgt = final[i].warrior.DAMAGE_VAR[k]
                        if(tgt.tactics == that && tgt.duration == 0 && tgt.tacticsItem == final[i].tactics){
                            tgt.duration = 1;
                            let r = new Report();
                            r.InitExecuteReport(3,final[i].warrior,`${final[i].tactics.name}${tgt.value}%`);
                            work2 = false;
                        }
                    }
                    if(work1){
                        let block1 = new OddBlock(self,that,final[i].tactics,odd,1,0,0);
                        block1.Init(final[i].warrior,true,3);
                    }
                    if(work2){
                        let block2 = new DamageBlock(self,that,'',true,value,false,false,1,0);
                        block2.Init(final[i].warrior,final[i].tactics,true,3);
                    }
                }
                self[tag] = 2;
            }
            let trigger = function(){
                let target = this.beExecuted;
                for(let i in target.ODD_VAR){
                    if(target.ODD_VAR[i].tactics == that && target.ODD_VAR[i].workTactics == this.triggerTactics && self[tag] == 2){
                        let r = new Report();
                        r.InitExecuteReport(3,self,`3`);
                        self[tag] = 3;
                    }
                }
            }
            let block = new ExecuteBlock(self,this,'',action,99);
            block.Init(self,false);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            for(let i in res){
                let block = new ActiveTacticsBlock(self,this,trigger,99,0,true,'');
                block.Init(res[i],false);
            }
            self[tag] = 2;
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 133,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let value = q.GetAdditionValue(self,16,'',0.12);
            
            let res = a.SelectTarget(self,5,false,true,3,false,false,this);
            let getHurt = function(){
                let target = this.beExecuted;
                if(!target.action){
                    for(let i in target.ATTR){
                        if(target.ATTR[i].tactics == that){
                            target.ATTR[i].value -= value;
                            let r = new Report();
                            let txt = `${target.ATTR[i].attrType}${Math.abs(target.ATTR[i].value)}(${q.GetAttr(target,target.ATTR[i].attrType)})`;
                            r.InitTwoTargetReport(3,self,target,``,txt)
                        }
                    }
                }
            }
            for(let i in res){
                let block = new AfterDamageBlock(self,this,getHurt,99,true,true,true,true,true,true,true);
                block.Init(res[i],'');
                for(let j=0;j<4;j++){
                    let attrType = "";
                    switch(j){
                        case 0 : attrType = "";break;
                        case 1 : attrType = "";break;
                        case 2 : attrType = "";break;
                        case 3 : attrType = "";break;
                    }
                    let block = new AttrBlock(self,this,attrType,0,99,false);
                    block.isBenefit = false;
                    block.Init(res[i],false,3);
                    
                }
                let r = new Report();
                r.InitExecuteReport(2,res[i],``);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 138,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let duration = 99;//
            let value = q.GetAdditionValue(self,25,'',0.12);//
            let index = 40;//40%
            let consume = 4;//

            let tag = '' + self.belong + self.position;
            let res = a.SelectTarget(self,5,true,false,3,true,false,this);
            let attack = function(){
                let tgt = this.beExecuted;
                let res = c.AttackDamageIndex(tgt,'',true,null);
                if(res > 1) res -=1;
                res = Math.floor(res * 100 / index);
                self[tag] += res;
                let r = new Report();
                r.InitExecuteReport(3,self,`${res}${self[tag]}`)
            }
            //
            let action = function(){
                if(self[tag] >= consume){
                    //
                    let work = true;
                    for(let i in self.STATUS){
                        if(self.STATUS[i].tactics.type == '' && self.STATUS[i].type == ''){
                            work = false;
                        }
                    }
                    if(work){
                        self[tag] -= consume;
                        let r = new Report();
                        r.InitExecuteReport(3,self,`${consume}${self[tag]}`);
                        a.LaunchAttack(self);
                    }
                    else{
                        let r = new Report();
                        r.InitExecuteReport(3,self,``);
                    }
                    
                    
                }
            }
            for(let i in res){
                let block = new DamageBlock(self,this,'',true,value,false,false,duration,0);
                block.Init(res[i],null,true,2);
                if(res[i] != self){
                    let block1 = new AttackExecuteBlock(self,this,attack,99,0,false);
                    block1.Init(res[i],false);
                }
                else{
                    let block = new AttackExecuteBlock(self,this,action,99,0,true,'');
                    block.Init(self,false);
                }
            }
            for(let i in res){
                let r = new Report();
                r.InitExecuteReport(2,res[i],``);
            }
            self[tag] = 0;
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 141,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let value = q.GetAdditionValue(self,32,'',0.1);//
            let proportion = 80;//
            let incline = 5;//

            //
            let tag = "" + "" + self.belong + self.position;
            self[tag] = 0;
            let action = function(){
                let tgt = this.beExecuted;
                //
                let damage = Math.floor(this.damageValue * value/100);
                self[tag] += damage;
                //
                if((tgt.hp + damage) < q.GetMaxHp(tgt)) tgt.hp += damage;
                else tgt.hp = q.GetMaxHp(tgt);
                let r = new Report();
                r.InitTwoTargetReport(3,self,tgt,``,`${value}%(${damage})(${tgt.hp})`);
            }
            //
            let res = a.SelectTarget(self,5,true,false,3,true,false,this);
            for(let i in res){
                let block = new AfterDamageBlock(self,this,action,99,true,true,true,true,true,true,true);
                block.Init(res[i],'');
            }
            let getHurt = function(){
                if(self.driver.turn > 1){
                    let index = (proportion + (self.driver.turn-2) * incline);
                    let damage = Math.floor(self[tag] * index/100);
                    if(damage > self.hp){
                        self.hp = 0;
                        self.alive = false;
                        damage = self.hp;
                    }
                    else self.hp -= damage;
                    
                    let r1 = new Report();
                    r1.InitExecuteReport(1,self,`${index}%(${self[tag]})`);
                    let r2 = new Report();
                    r2.InitTwoTargetReport(1,self,self,``,`${damage}(${self.hp})`);
                    
                }
                self[tag] = 0;
            }
            let block = new ExecuteBlock(self,this,'',getHurt,99);
            block.Init(self,false);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            this.STATISTICS.times += 1;
        }
    },
]

export default qunSArr;