import Query from "../js/query";
import Report from "../js/report";

class AttrBlock{
    /**
     * 
     * @param {object} applicator 
     * @param {object} tactics 
     * @param {string} attrType 
     * @param {number} value 
     * @param {number} [duration=99] 
     * @param {boolean} isRepeat ,
     * @param {boolean} isBenefit 
     */
    constructor(applicator,tactics,attrType,value,duration,isRepeat,isBenefit){
        this.applicator = applicator;
        this.tactics = tactics;
        this.attrType = attrType;
        this.value = value;
        this.duration = duration;
        this.isBenefit = isBenefit == undefined ? (this.value > 0 ? true : false) : isBenefit;
        this.isRepeat = isRepeat == undefined ? false : isRepeat;
        //
        this.owner = null;
    }
    Init(target,needReport = true,reportLevel = 3){
        //needReport
        this.owner = target;
        //
        let work = true;
        if(this.isRepeat){
            for(let i in target.ATTR){
                if(target.ATTR[i].tactics == this.tactics && target.ATTR[i].attrType == this.attrType){
                    target.ATTR[i].value += this.value;
                    target.ATTR[i].duration = this.duration;//

                    if(needReport){
                        let q = new Query();
                        let report = new Report();
                        let text = `${this.attrType}`;
                        if(target.ATTR[i].value > 0){
                            text += `${target.ATTR[i].value}(${q.GetAttr(target,this.attrType)})`;
                        }
                        else text += `${Math.abs(target.ATTR[i].value)}(${q.GetAttr(target,this.attrType)})`;
                        report.InitExecuteReport(reportLevel,target,text);
                    }
                    work = false;
                }
            }
        }
        
        if(work){
            //
            let temp = [];
            for(let i in target.ATTR){
                //
                if(target.ATTR[i].attrType == this.attrType && this.tactics.type == target.ATTR[i].tactics.type){
                    //
                    if(this.isBenefit && target.ATTR[i].isBenefit){
                        //
                        if(this.value <= target.ATTR[i].value){
                            let report = new Report();
                            let str = `${target.ATTR[i].attrType}`;
                            report.InitExecuteReport(3,target,str);
                            return false;
                        }
                        else{
                            let r = new Report();
                            r.InitExecuteReport(3,target,`${target.ATTR[i].attrType}`)
                        }
                    }
                    //
                    else if(!this.isBenefit && !target.ATTR[i].isBenefit){
                        //
                        if(this.value >= target.ATTR[i].value){
                            let report = new Report();
                            let str = `${target.ATTR[i].attrType}`;
                            report.InitExecuteReport(3,target,str);
                            return false;
                        }
                        else{
                            let r = new Report();
                            r.InitExecuteReport(3,target,`${target.ATTR[i].attrType}`)
                        }
                    }
                    else temp.push(target.ATTR[i]);
                }
                else temp.push(target.ATTR[i]);
            }
            target.ATTR = temp;
            target.ATTR.push(this);
        } 
        if(needReport && work){
            let q = new Query();
            let report = new Report();
            let text = `${this.attrType}`;
            if(this.isBenefit){
                text += `${this.value}(${q.GetAttr(target,this.attrType)})`;
            }
            else text += `${Math.abs(this.value)}(${q.GetAttr(target,this.attrType)})`;
            report.InitExecuteReport(reportLevel,target,text);
        }
        return true;
    }
    InitTwoTargetReport(tactics,attrType,target1,target2,isBenefit,reportLevel = 3){
        let q = new Query();
        for(let i in target2.ATTR){
            if(target2.ATTR[i].tactics == tactics && target2.ATTR[i].attrType == attrType){
                let str = isBenefit ? "" : "";
                let attr = target2.ATTR[i].attrType;
                let r = new Report();
                let txt = `${attr}${str}${isBenefit ? target2.ATTR[i].value : Math.abs(target2.ATTR[i].value)}(${q.GetAttr(target2,attr)})`;
                r.InitTwoTargetReport(reportLevel,target1,target2,`${tactics.name}`,txt,true);
            }
        }
    }
}

export default AttrBlock;