import DamageBlock from "../block/damage";
import StatusBlock from "../block/status.js";
import AfterDamageBlock from "../block/execute1.js";
import Attack from '../js/attack.js';
import Count from '../js/count.js';
import Query from '../js/query.js';
import Report from '../js/report.js'
import DamageStatus from "../block/damageStatus.js";
import ExecuteBlock from "../block/execute.js";
import AttrBlock from "../block/attr.js";
import OddBlock from "../block/odd.js";
import ExecuteSysBlock from "../block/executeSys.js";
import IgnoreBlock from "../block/ignore";
let shuSArr = [
    //
    {
        id : 1,
        name : "",
        type : "",
        odd : 100,
        range : 3,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : true,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let odd = 50;//
            let cureValue = 68;//
            let cureIndex = 0.6;//
            
            let res = a.SelectTarget(self,3,true,false,3,true,true,this);
            for(let i in res){
                let action = function(){
                    let realOdd = q.GetMoraleOdd(self,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let cure = c.CountCure(self,res[i],cureValue,cureIndex,that);
                        let report = new Report();
                        report.InitTwoTargetReport(3,self,res[i],``,`${cure}(${res[i].hp + cure})`,false);
                        a.GetCure(self,res[i],cure,that,true,report);
                    }
                }
                let block = new AfterDamageBlock(self,this,action,99,true,true,true,true,true,true,true);
                let work = block.Init(res[i],"");
                if(work){
                    let report = new Report();
                    report.InitExecuteReport(2,res[i],``);
                }
                
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 40,
        name : "",
        type : "",
        odd : 120,
        range : 4,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let damageIndex = 120;//
            let addition = 40;//
            if(self.driver.version == ''){
                addition = 25;
            }

            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this);
            //
            let block = new DamageBlock(self,this,"",true,addition,false,false,0);
            block.Init(self);
            //
            let block1 = new StatusBlock(self,this,"",0,0);
            block1.Init(self,false);
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageIndex,"",false,this);
                a.GetHurt(self,res[i],damage,false,"",this);
            }
            
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 23,
        name : "",
        type : "",
        odd : 50,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let damageIndex = 150;//
            let addition = 20;//
            let ad = q.GetAttr(self,"");
            addition += Math.floor((ad - 120)*0.18);
            
            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            
            for(let j =0;j<2;j++){
                let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this);
                for(let i in res){
                    let damage = c.CountDamage(self,res[i],damageIndex,"",false,this);
                    a.GetHurt(self,res[i],damage,false,"",this);
                    if(j == 0){
                        let block1 = new DamageBlock(self,this,"",false,addition,false,false,2);
                        block1.Init(res[i]);
                        let block2 = new DamageBlock(self,this,"",false,addition,false,false,2);
                        block2.Init(res[i]);
                    }
                }
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 48,
        name : "",
        type : "",
        odd : 35,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let c = new Count();
            let a = new Attack();
            let value = 30 + Math.floor((q.GetAttr(self,"")-80)*0.16);
            let value1 = 115 + Math.floor((q.GetAttr(self,"")-80)*1.15);
            let value2 = 133 + Math.floor((q.GetAttr(self,"")-80)*1.25);

            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,false,true,2,false,true,this);
            for(let i in res){
                let block1 = new DamageBlock(self,this,"",true,-value,false,false,2,0);
                block1.Init(res[i],null,true);
                let block2 = new DamageBlock(self,this,"",true,-value,false,false,2,0);
                block2.Init(res[i],null,true);
            }
            let action = function(){
                let target = this.beExecuted;
                //
                let avail = false;
                for(let i in target.SPECIAL_STATUS){
                    if(target.SPECIAL_STATUS[i].tactics == that){
                        avail = true; break;
                    }
                }
                if(avail){
                    let damage = c.CountDamage(self,target,value2,"",false,this.tactics,"");
                    a.GetHurt(self,target,damage,false,"",this.tactics,false);
                    let r = new Report();
                    r.InitTwoTargetReport(2,self,target,``,`${damage}(${target.hp})`);
                }
                else{
                    //
                    let temp = [];
                    for(let i in target.EXECUTE){
                        if(target.EXECUTE[i].tactics != that) temp.push(target.EXECUTE[i]);
                    }
                    target.EXECUTE = temp;
                }
            }
            //
            for(let i in res){
                //
                let block = new DamageStatus(self,this,'',value1,"",2,0,true);
                let flag = block.Init(res[i],true);
                if(flag){
                    let block = new ExecuteBlock(self,this,"",action,2);
                    block.Init(res[i]);
                }
            }

            
            
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 52,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let c = new Count();
            let a = new Attack();
            let index = 6;
            

            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,false,true,2,false,false,this);
            for(let i in res){
                let block = new StatusBlock(self,this,'',2,0);
                let work = block.Init(res[i],false);
                if(work){
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],``);
                }
                //
                let block1 = new AttrBlock(self,this,"",0,99);
                let work1 =  block1.Init(res[i],false);
                if(work1){
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],`()`);
                }
            }
            let action = function(){
                for(let j in res){
                    let target = res[j];
                    for(let i in target.ATTR){
                        if(target.ATTR[i].tactics == that){
                            let value = Math.floor(target.attr.tact * index/100);
                            target.ATTR[i].value -= value;
                            let r = new Report();
                            let txt = `${index*self.driver.turn}%(${Math.abs(target.ATTR[i].value)})(${q.GetAttr(target,"")})`
                            r.InitTwoTargetReport(1,self,target,``,txt);
                        }
                    }
                }
            }
            let block = new ExecuteSysBlock(self,this,"",action,99);
            block.Init(self);
            
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 78,
        name : "",
        type : "",
        odd : 100,
        range : 2,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : false,
        selectEnemy : false,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let c = new Count();
            let a = new Attack();
            let index = 120;
            let odd = 75;//
            
            let fri = a.SelectTarget(self,3,true,false,2,false,true,that);
            for(let i in fri){
                if(fri[i].TacticsArr[0].type == ''){
                    let block = new OddBlock(self,this,fri[i].TacticsArr[0],0,99,0);
                    let work = block.Init(fri[i],false);
                    if(work){
                        let r = new Report();
                        r.InitExecuteReport(2,fri[i],`${fri[i].TacticsArr[0].name}()`)
                    }
                }
            }
            let action = function(){
                //
                for(let i in fri){
                    for(let j in fri[i].ODD_VAR){
                        if(fri[i].ODD_VAR[j].tactics == that){
                            fri[i].ODD_VAR[j].value = 0;
                            fri[i].ODD_VAR[j].skipTime = 0;
                        }
                    }
                }
                if(self.driver.turn%2 != 0){
                    let r = new Report();
                    r.InitExecuteReport(2,self,``);
                    
                    let target = fri[Math.floor(Math.random()*fri.length)];
                    for(let i in target.ODD_VAR){
                        if(target.ODD_VAR[i].tactics == that){
                            target.ODD_VAR[i].value = index;
                            let r = new Report();
                            r.InitExecuteReport(3,target,`${target.ODD_VAR[i].workTactics.name}${index}%`)
                            if(target.ODD_VAR[i].workTactics.wait > 0){
                                let realOdd = q.GetMoraleOdd(self,odd);
                                let rand = Math.floor(Math.random()*100);
                                if(rand < realOdd){
                                    target.ODD_VAR[i].skipTime = 1;
                                    let r = new Report();
                                    r.InitExecuteReport(3,target,`${target.ODD_VAR[i].workTactics.name}`)
                                }
                            }
                        }
                    }
                }
            }
            let block = new ExecuteBlock(self,this,'',action,99);
            block.Init(self);
            
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 88,
        name : "",
        type : "",
        odd : 40,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 3,
        arms : [''],
        wait : 1,
        execute(self){
            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let damageIndex = 190;//
            
            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this);
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageIndex,"",false,this);
                a.GetHurt(self,res[i],damage,false,"",this);
            }
            for(let i in res){
                let sta = new StatusBlock(self,this,"",1);
                sta.Init(res[i]);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 92,
        name : "",
        type : "",
        odd : 100,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let odd = 60;//
            let index1 = q.GetAdditionValue(self,34,'',0.08);
            let index2 = 80;

            let getHurt = function(){
                let target = this.damageFromWho;//
                let realOdd = q.GetMoraleOdd(self,odd);
                let rand = Math.floor(Math.random()*100);
                //
                for(let i in self.DAMAGE_VAR){
                    if(self.DAMAGE_VAR[i].tactics == that && self.DAMAGE_VAR[i].value < 0){
                        self.DAMAGE_VAR[i].value += index2/4;
                        let r = new Report();
                        r.InitExecuteReport(3,self,`${Math.abs(self.DAMAGE_VAR[i].value)}%`)
                    }
                }
                //
                if(rand < realOdd){
                    let block = new DamageBlock(self,that,'',true,-index1,true,false,99,0);
                    let work = block.Init(target,null,false,3);
                    if(work){
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,target,``,`${index1}%`);
                    }
                }
            }
            let action = function(){
                let r = new Report();
                r.InitExecuteReport(2,self,``);
                let tag = true;
                //
                for(let i in self.DAMAGE_VAR){
                    if(self.DAMAGE_VAR[i].tactics == that){
                        self.DAMAGE_VAR[i].value = -index2;
                        tag = false;
                        let r = new Report();
                        r.InitExecuteReport(3,self,`${index2}%`)
                    }
                }
                //
                if(tag){
                    let block = new DamageBlock(self,that,'',false,0,false,false,99,0);
                    block.Init(self,null,true,3);
                }
            }
            let block1 = new AfterDamageBlock(self,this,getHurt,99,true,true,true,true,true,true,true);
            block1.Init(self,'');
            let block = new ExecuteBlock(self,this,'',action,99);
            let work = block.Init(self,false);
            if(work){
                let block = new DamageBlock(self,that,'',false,0,false,false,99,0);
                block.Init(self,null,false);
                let r  = new Report();
                r.InitExecuteReport(2,self,``);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 152,
        name : "",
        type : "",
        odd : 50,
        range : 4,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let damageValue = 280;
            let damageValue2 = 80;//
            let ignoreDe = 100;

            let tag = '' + self.belong + self.position;
            self[tag] = 0;

            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this);
            let stsBlock = new StatusBlock(self,this,'',99,0);
            let work = stsBlock.Init(self,false);
            if(work){
                let r = new Report();
                r.InitExecuteReport(2,self,``);
            }
            let ignBlock = new IgnoreBlock(self,this,'',ignoreDe,1,0,null);
            ignBlock.Init(self,true,2);
            let that = this;
            let action = function(){
                if(this.damageFromTactics != that && self[tag] < 3){
                    self[tag] += 1;
                    let r = new Report();
                    r.InitExecuteReport(3,self,`(${3-self[tag]})`);
                    let damage = c.CountDamage(self,this.beExecuted,damageValue2,'',false,that,'');
                    a.GetHurt(self,this.beExecuted,damage,false,'',that,true,null,true);
                    
                }
            }
            let clearStatus = function(){
                for(let i in self.ally){
                    let temp = [];
                    for(let j in self.ally[i].AFTER_DAMAGE){
                        if(self.ally[i].AFTER_DAMAGE[j].tactics == that){
                            let r = new Report();
                            r.InitExecuteReport(2,self.ally[i],``)
                        }
                        else temp.push(self.ally[i].AFTER_DAMAGE[j])
                    }
                    self.ally[i].AFTER_DAMAGE = temp;
                }
                for(let i in self.enemy){
                    let temp = [];
                    for(let j in self.enemy[i].AFTER_DAMAGE){
                        if(self.enemy[i].AFTER_DAMAGE[j].tactics == that){
                            let r = new Report();
                            r.InitExecuteReport(2,self.enemy[i],``)
                        }
                        else temp.push(self.enemy[i].AFTER_DAMAGE[j])
                    }
                    self.enemy[i].AFTER_DAMAGE = temp;
                }
            }
            let block = new ExecuteBlock(self,this,'',clearStatus,1);
            block.Init(self,true);
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageValue,"",false,this,null);
                a.GetHurt(self,res[i],damage,false,"",this);
                //
                let block = new AfterDamageBlock(self,this,action,1,true,false,true,true,true,true,true);
                block.Init(res[i],"");

            }
            for(let i in res){
                let staBlock = new StatusBlock(self,this,"",1);
                staBlock.Init(res[i]);
            }
            //
            let temp = [];
            let temp2 = [];
            for(let i in self.STATUS){
                if(self.STATUS[i].tactics == this){
                    let r = new Report();
                    r.InitExecuteReport(2,self,``);
                }
                else{
                    temp.push(self.STATUS[i])
                }
            }
            self.STATUS = temp;
            for(let i in self.IGNORE){
                if(self.IGNORE[i].tactics == this){
                    let r = new Report();
                    r.InitExecuteReport(2,self,``);
                }
                else temp2.push(self.IGNORE[i]);
            }
            self.IGNORE = temp2;
            this.STATISTICS.times += 1;
        }
    }
    
]

export default shuSArr;